.error-text {
  font-size: 14px;
  color: red;
  padding-left: 10px;
  padding-top: 10px;
  text-align: left;
}

.step-wrapper {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mob-banking-wrapper {
  width: 50%;
  display: flex;
  align-items: center;
  img {
    width: 100%;
  }
}
.row {
  display: flex;
}

.info-container {
  display: flex;
  margin-top: 16px;
  padding-left: 16px;
  a {
    color: #333;
    font-size: 13px;
  }
}

.bold {
  font-weight: bold;
}

input.error {
  border: 1px solid red !important;
}

.spinner-container {
  display: flex;
  align-items: center;
  width: 50%;
  justify-content: center;
  img {
    width: 50px;
  }
}

button {
  cursor: pointer;
}

input {
  &::placeholder {
    font-style: italic;
    font-size: 18px;
    font-family: 'PT Sans', sans-serif;
  }
}