.loading-area {
  width: 450px;
  .header-area {
    background-color: #333;
    max-width: 414px;
    width: 100%;
    padding: 18px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    .language-selector-container {
      display: flex;
      align-items: center;
      position: relative;
      .language-type {
        color: #ffffff;
        font-size: 14px;
        margin-right: 25px;
      }
      .arrow-icon {
        position: absolute;
        right: 0px;
        top: 0px;
        img {
           width: 20px;
        }
      }
    }
  }
  .form-area {
    .login-pseudomodal {
      .secure-title {
        font-weight: bold;
        font-size: 19px;
        color: #3f3c3c;
      }
      .change-user a {
        color: #999;
        font-size: 13px;
        text-decoration: underline;
        text-transform: uppercase;
      }
      
      max-width: 330px;
      width: 100%;
      min-height: 270px;
      padding: 40px 60px;
      background: #ffffff;
      .login-title {
        font-size: 25px;
        margin-bottom: 7px;
        text-align: center;
      }
      .field-description {
        text-align: left;
        margin: 41px 0 17px 0;
      }
      .field-input {
        min-height: 70px;
        input {
          width: 100%;
          max-width: 310px;
          height: 40px;
          padding: 0 10px;
          color: #555555;
          border-radius: 0px;
          border: 1px solid #ccc;
          &:focus {
          border: 1px solid #ccc !important;
          border-radius: none;
          outline: none !important;
          }
        }
        input.error {
          border: 1px solid red;
        }
      }
      .button-confirm {
        background-color: #35a000;
        border-radius: 3px;
        border: 0 none;
        color: #ffffff;
        font-size: 13px;
        height: 40px;
        line-height: 40px;
        min-width: 130px;
        padding: 0 20px;
        text-transform: uppercase;
        text-align: center;
        font-weight: bold;
        width: 100%;
        margin-top: 10px;
      }
    }
  }
}