.image-secure {
  .image-secure-wrapper {
    width: 687px;
    padding: 20px;
    background: #ffffff;
    border-radius: 10px;
  }
  .loading-area {
    width: 650px;
    .header-area {
      max-width: 100%;
    }
    .form-area {
      width: 100%;
      .login-pseudomodal {
        width: 100%;
        padding: 40px 18px;
        max-width: 100%;
        .button-confirm {
          width: 332px;
        }
        .bottom-description {
          margin-top: 16px;
          font-size: 14px;
        }
        .bottom-info-container {
          width: 100%;
          display: flex;
          font-size: 14px;
          .bottom-info-item {
            width: 50%;
            .info-title {
              margin-top: 16px;
              margin-bottom: 12px;
            }
          }
        }
      }
    }
  }
}